.tree-item {
  text-align: left;
  cursor: pointer;
}

.tree-item__header {
  height: 1.5rem;
  border-bottom: 1px dashed rgba(192, 192, 192, 0.3);
}

.tree-item__header-left-side {
  float: left;
  text-align: left;
  width: 50%;
  height: 1.5rem;
}

.tree-item__header-right-side {
  float: right;
  text-align: right;
  width: 50%;
  height: 1.5rem;
}

.tree-item__content {
  margin: 0.3rem 0 0 1.25rem;
}
